<template>
	<view>
		<navbar v-model="screenHeight" title-color="#ffffff" background="#1b43c4" :iSimmersive="false" :placeholder="true" title="订单统计"></navbar>
		<view class="list-wrap">
			<mescroll-body 
			ref="mescrollRef" 
			@init="mescrollInit" 
			@down="downCallback" 
			@up="upCallback"
			:height="screenHeight+'px'"
			>
					<view class="list-box">
						<view class="list-item" v-for="(item, index) in result" :key="index">
							<view class="item-box">
								<view class="t">
									<text>订单号：{{item.order_no}}</text>
									<text>返佣：￥{{item.amount}}</text>
								</view>
								<view class="m">
									<view class="avatar">
										<image v-if="item.avatar" :src="item.avatar"></image>
									</view>
									<view class="ll">
										<text>{{item.nickname}}</text>
										<text>{{item.time}}</text>
									</view>
								</view>
							</view>
						</view>
					</view>
			</mescroll-body>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
	import navbar from "@/components/navbar/navbar";
	export default {
		mixins: [MescrollMixin],
		components: {
			navbar
		},
		data(){
			return {
				screenHeight: 0,
				result: []
			};
		},
		methods: {
			changeData(index){
				this.isActive = index;
				this.downCallback();
			},
			downCallback(){
				setTimeout(()=>{
					this.mescroll.resetUpScroll();
				},1200);
			},
			triggerDownScroll(){
				this.mescroll.triggerDownScroll();
			},
			upCallback(page) {
				this.$http.getSpreadPromotionOrder({
					page: page.num
				}).then((result) => {
					this.mescroll.endByPage(result.data.list.length, result.data.total);
					if(result.status==1){
						if(page.num == 1) this.result = [];
						this.result = this.result.concat(result.data.list);
					}else if(result.status == -1){
						this.mescroll.endErr();
					}
				}).catch((error) => {
					this.mescroll.endErr();
				});
			},
		}
	}
</script>

